<template>
	<view class="content">
		<u-navbar :is-back="false" :border-bottom="false">
			<view class="slot-wrap">
				<view class="search-wrap">
					<u-search height="56" disabled></u-search>
				</view>
				<view class="navbar-right">
					<view class="message-box right-item">
						<u-icon name="chat" size="42" color="#666"></u-icon>
						<u-badge size="mini" :is-dot="true" :offset="[-6, -6]"></u-badge>
					</view>
				</view>
			</view>
		</u-navbar>
		<!-- 菜单 -->
		<view class="top-warp">
			<view class="tabs">
				<u-sticky>
					<!-- <u-tabs :list="tabs" :current="tabCurrent" @change="tabChange" height="88" /> -->
					<me-tabs v-model="tabIndex" :tabs="tabs" @change="tabChange" :tab-width="130"></me-tabs>
				</u-sticky>
			</view>
			
		</view>
		<mescroll-item ref="mescrollItem" v-for="(tab,i) in tabs" :id="tab.id" :key="i" :i="i" :index="tabIndex" :tabs="tabs"></mescroll-item>
	</view>
</template>

<script>
	import MescrollItem from "./item.vue";
	import MescrollMoreMixin from "@/components/mescroll-uni/mixins/mescroll-more.js";
	import {newsTab} from '@/common/api/index.js'
	export default {
		mixins: [MescrollMoreMixin], // 多个mescroll-body写在子组件时, 则使用mescroll-more.js补充子组件的页面生命周期
		components: {
			MescrollItem
		},
		data() {
			return {
				tabCurrent: 0,
				tabs: [],
				tabIndex: 0 // 当前tab下标,必须与mescroll-more.js对应,所以tabIndex是固定变量,不可以改为其他的名字
			}
		},
		onShow() {
			// 返回刷新: https://www.mescroll.com/uni.html#note 第二点
			// if(this.canReset){
			// 	let curMescroll = this.getMescroll(this.tabIndex)
			// 	curMescroll && curMescroll.resetUpScroll()
			// }
			// this.canReset = true
		},
		onLoad() {
			this.getTabList()
		},
		methods:{
			getTabList() {
				newsTab().then(res => {
					// console.log(res)
					this.tabs = res.data
					this.tabs.unshift({id: '', name: '推荐'})
				})
			},
			
		}
	}
</script>

<style lang="scss" scoped>
	.container {
		background-color: #F6F7F8;
	}
	
	.banner {
		padding: 24rpx 0;
	}
	
	image {
		width: 216rpx;
		height: 384rpx;
	}
	
	.img-box {
		display: flex;
		flex-wrap: wrap;
	}
	
	.navbar-right {
		// margin-right: 24rpx;
		display: flex;
	}
	
	.search-wrap {
		// margin: 0 20rpx;
		flex: 1;
		margin-right: 24rpx;
	}
	
	.right-item {
		// margin: 0 12rpx;
		position: relative;
		color: #ffffff;
		display: flex;
	}
	
	.message-box {}
	
	.slot-wrap {
		display: flex;
		align-items: center;
		flex: 1;
		padding: 0 24rpx;
	}
	
	.map-wrap {
		display: flex;
		align-items: center;
		padding: 4px 6px;
		background-color: rgba(240, 240, 240, 0.35);
		color: #fff;
		font-size: 22rpx;
		border-radius: 100rpx;
		// margin-left: 30rpx;
	}
	
	.map-wrap-text {
		padding: 0 6rpx;
	}
	.tabs{
		height: 88rpx;
	}
</style>
